/*! modern-normalize v2.0.0 | MIT License | https://github.com/sindresorhus/modern-normalize */
*, :before, :after {
  box-sizing: border-box;
}

html {
  -webkit-text-size-adjust: 100%;
  tab-size: 4;
  font-family: system-ui, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
  line-height: 1.15;
}

body {
  margin: 0;
}

hr {
  color: inherit;
  height: 0;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button {
  height: auto;
}

::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

.example {
  color: #2f2f37;
  background-color: #fff;
  max-width: 960px;
  padding: 16px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.example code {
  color: #fff;
  background-color: #ff6b0a;
  border-radius: 2px;
  padding: 2px 6px;
  font-family: monospace;
  font-size: 16px;
  font-weight: 700;
}

.example .background {
  background-image: url("logo.b0cae28f.png");
  background-position: center;
  background-size: cover;
  height: 240px;
}

input {
  color: #698269;
  width: 375px;
  padding: 3px 15px;
  font-size: 20px;
}

textarea:focus, input:focus {
  outline: none;
}

button {
  color: #3f3f44;
  background-color: #fcf8f3;
  border: none;
  width: 175px;
  padding: 10px 0;
  font-size: 20px;
  transition: color .25s cubic-bezier(.4, 0, .2, 1), background-color .25s cubic-bezier(.4, 0, .2, 1);
}

button:hover {
  color: #fcf8f3;
  box-shadow: none;
  background-color: #e7ab9a;
}

.search-bar {
  z-index: 999;
  background-color: #482121;
  width: 100%;
  height: auto;
  position: fixed;
  top: 0;
  box-shadow: 0 15px 40px -15px #00000080;
}

.search-form {
  justify-content: space-between;
  width: 565px;
  height: auto;
  margin: 0 auto;
  padding: 10px 0;
  display: flex;
}

.gallery {
  flex-flow: wrap;
  gap: 20px;
  width: 100%;
  min-width: 1440px;
  margin: 80px 20px;
  display: flex;
}

.photo-card {
  background-color: #fffbeb;
  flex-basis: calc(25% - 25px);
  height: auto;
  box-shadow: 0 0 6px -1px #00000059;
}

img {
  object-fit: cover;
  width: 100%;
  height: 78%;
  display: block;
}

.info {
  letter-spacing: 1.07px;
  color: #862b0d;
  font-variant: small-caps;
  flex-flow: row;
  justify-content: space-around;
  font-size: 14px;
  display: flex;
}

.info-item {
  flex-direction: column;
  justify-content: flex-start;
  gap: 4px;
  padding-top: 4px;
  display: flex;
}

.btn-wrapper {
  width: 100%;
  height: auto;
  position: fixed;
  bottom: 40px;
}

.load-more.is-hidden {
  display: none;
}

.load-more {
  color: #fcf8f3;
  background-color: #482121;
  width: 300px;
  margin: 0 auto;
  padding: 15px 0;
  transition: color .25s cubic-bezier(.4, 0, .2, 1), background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

button.load-more:hover {
  color: #fcf8f3;
  background-color: #e7ab9a;
}

